import { escapeHTML, escapeAttribute } from "@wordpress/escape-html";
import Sanitizer from "../sanitizing";

import { useBlockProps } from '@wordpress/block-editor';

export default (props) => {
	const {
		currentImageURL,
		facebookLink,
		linkedInLink,
		twitterLink,
		headline,
		authorBio,
	} = props.attributes;

	const sanitizer = Sanitizer();

	const blockProps = props.editor ? useBlockProps({className: 'sptcrb__abouttheauthor__block__wrapper'}) : useBlockProps.save({className: 'sptcrb__abouttheauthor__block__wrapper'});

	return (
		<div {...blockProps}>
			<div className="sptcrb__abouttheauthor__block__left">
				{!!currentImageURL && (
					<div
						className="sptcrb__abouttheauthor__block__left__image"
						style={{
							backgroundImage:
								"url(" + sanitizer.sanitizeLink(currentImageURL) + ")",
						}}
					></div>
				)}
				{!currentImageURL && (
					<div className="sptcrb__abouttheauthor__block__left__image"></div>
				)}
				<div className="sptcrb__abouttheauthor__block__left__some__wrapper">
					{!!facebookLink && (
						<a
							href={escapeAttribute(facebookLink)}
							rel="noopener noreferrer"
							target="_blank"
						>
							<div className="sptcrb__abouttheauthor__block__left__some__icon__wrapper sptcrb__abouttheauthor__block__left__some__icon__facebook">
								<i className="fab fa-facebook-f"></i>
							</div>
						</a>
					)}
					{!!twitterLink && (
						<a
							href={escapeAttribute(twitterLink)}
							rel="noopener noreferrer"
							target="_blank"
						>
							<div className="sptcrb__abouttheauthor__block__left__some__icon__wrapper sptcrb__abouttheauthor__block__left__some__icon__twitter">
								<i className="fab fa-twitter"></i>
							</div>
						</a>
					)}
					{!!linkedInLink && (
						<a
							href={escapeAttribute(linkedInLink)}
							rel="noopener noreferrer"
							target="_blank"
						>
							<div className="sptcrb__abouttheauthor__block__left__some__icon__wrapper sptcrb__abouttheauthor__block__left__some__icon__linkedin">
								<i className="fab fa-linkedin-in"></i>
							</div>
						</a>
					)}
				</div>
			</div>
			<div className="sptcrb__abouttheauthor__block__right">
				<span>{escapeHTML(headline)}</span>
				<p>{escapeHTML(authorBio)}</p>
			</div>
		</div>
	);
};
