/**
 * BLOCK: superb-blocks
 *
 * Registering a basic block with Gutenberg.
 * Simple block, renders and saves the same content without any interactivity.
 */

//  Import CSS.
import "./editor.scss";
import "./style.scss";
import editf from "./edit.jsx";
import savef from "./save.jsx";

import json from "./block.json";

const { name, ...settings } = json;

const { __ } = wp.i18n; // Import __() from wp.i18n
const { registerBlockType } = wp.blocks; // Import registerBlockType() from wp.blocks

/**
 * Register: aa Gutenberg Block.
 *
 * Registers a new block provided a unique name and an object defining its
 * behavior. Once registered, the block is made editor as an option to any
 * editor interface where blocks are implemented.
 *
 * @link https://wordpress.org/gutenberg/handbook/block-api/
 * @param  {string}   name     Block name.
 * @param  {Object}   settings Block settings.
 * @return {?WPBlock}          The block, if it has been successfully
 *                             registered; otherwise `undefined`.
 */

export const baseAuthorObject = {
	bgImageId: { type: "number", default: 0 },
	currentImageURL: { type: "string", default: "" },
	headline: { type: "string", default: "Author Name" },
	authorBio: { type: "string", default: "Author Description" },
	linkedInLink: { type: "string", default: "" },
	facebookLink: { type: "string", default: "" },
	twitterLink: { type: "string", default: "" },
};

registerBlockType(name, {
	...settings,
	keywords: [
		__("superb author box"),
		__("about the author"),
		__("author block"),
		__("author box"),
	],
	attributes: {
		...baseAuthorObject,
		selectedAuthor: {
			type: "number",
			default: -1,
		},
	},
	example: {
		attributes: {
			headline: "Superb Author",
			authorBio:
				"Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas id euismod velit, ac imperdiet turpis. Fusce faucibus vulputate neque, eget tempor metus fringilla eget. Integer accumsan tempus massa, sit amet scelerisque est pharetra a. Sed egestas, erat et dapibus semper, turpis orci vulputate urna, at ornare ante dolor at enim.",
			linkedInLink: "#",
			twitterLink: "#",
			facebookLink: "#",
		},
	},

	/**
	 * The edit function describes the structure of your block in the context of the editor.
	 * This represents what the editor will render when the block is used.
	 *
	 * The "edit" property must be a valid function.
	 *
	 * @link https://wordpress.org/gutenberg/handbook/block-api/block-edit-save/
	 *
	 * @param {Object} props Props.
	 * @returns {Mixed} JSX Component.
	 */
	edit: editf,

	/**
	 * The save function defines the way in which the different attributes should be combined
	 * into the final markup, which is then serialized by Gutenberg into post_content.
	 *
	 * The "save" property must be specified and must be a valid function.
	 *
	 * @link https://wordpress.org/gutenberg/handbook/block-api/block-edit-save/
	 *
	 * @param {Object} props Props.
	 * @returns {Mixed} JSX Frontend HTML.
	 */
	save: savef,
});
