import { InspectorControls } from "@wordpress/block-editor";
import { Fragment } from "@wordpress/element";
import {
	PanelBody,
	TextControl,
	TextareaControl,
	SelectControl,
} from "@wordpress/components";

import { baseAuthorObject } from "./index.js";

import savef from "./save.jsx";
import Sanitizer from "../sanitizing";
import defaultView, { defaultTitle, defaultPanel } from "../defaultview.js";

export default (props) => {
	const sanitizer = Sanitizer();
	const { __ } = wp.i18n;
	const { select } = wp.data;
	const { MediaUpload, MediaUploadCheck } = wp.blockEditor;
	const { Button } = wp.components;

	const { attributes, setAttributes } = props;
	props.editor = true;
	const { selectedAuthor } = attributes;
	const ALLOWED_MEDIA_TYPES = ["image"];
	const { getAuthors } = select("core");

	const instructions = __(
		"To change the author image, you need permission to upload media.",
		"sptcrb"
	);

	const defaultAuthor = -1;

	const onUpdateImage = (image) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			bgImageId: image.id,
			currentImageURL: sanitizer.sanitizeLink(image.url),
		});
	};

	const onUpdateHeadline = (value) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			headline: value,
		});
	};

	const onUpdateBio = (value) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			authorBio: value,
		});
	};

	const onUpdateLinkedIn = (value) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			linkedInLink: sanitizer.sanitizeLink(value),
		});
	};

	const onUpdateFacebook = (value) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			facebookLink: sanitizer.sanitizeLink(value),
		});
	};

	const onUpdateTwitter = (value) => {
		setAttributes({
			selectedAuthor: defaultAuthor,
			twitterLink: sanitizer.sanitizeLink(value),
		});
	};

	const onChangeUpdateAuthor = (value) => {
		const selectedAuthor = sanitizer.sanitizeNumber(value);
		if (selectedAuthor === -1) {
			setAttributes({
				selectedAuthor: selectedAuthor,
				headline: baseAuthorObject.headline.default,
				authorBio: baseAuthorObject.authorBio.default,
				currentImageURL: "",
				twitterLink: "",
				facebookLink: "",
				linkedInLink: "",
			});
		} else {
			const authors = getAuthorList();
			setAttributes({
				selectedAuthor: selectedAuthor,
				headline: authors[selectedAuthor].name,
				authorBio: authors[selectedAuthor].description,
				currentImageURL: sanitizer.sanitizeLink(
					authors[selectedAuthor].avatar_urls[96]
				),
			});
		}
	};

	const getAuthorList = () => {
		return getAuthors();
	};

	return (
		<Fragment>
			<InspectorControls>
				{defaultPanel()}
				<PanelBody title="Author Information" initialOpen={true}>
					<small>
						Select a WordPress User to gather information from the user.
					</small>
					<SelectControl
						label="Author"
						options={[
							{ label: "Custom", value: -1 },
							...getAuthorList().map((author, index) => {
								return {
									label: author.name,
									value: index,
								};
							}),
						]}
						value={selectedAuthor}
						onChange={onChangeUpdateAuthor}
					/>
					<label className="components-base-control__label sptcrb-control-label">
						Author Image
					</label>
					<div className="spbatab-image-preview-wrapper">
						<img width="50" height="auto" src={attributes.currentImageURL} />
					</div>
					<MediaUploadCheck fallback={instructions}>
						<MediaUpload
							title={__("Author image", "superb-blocks/about-the-author-block")}
							onSelect={onUpdateImage}
							allowedTypes={ALLOWED_MEDIA_TYPES}
							value={attributes.bgImageId}
							render={({ open }) => (
								<Button
									className={"sptcrb__abouttheauthor__backend__image__toggle"}
									onClick={open}
								>
									{__(
										"Set author image",
										"superb-blocks/about-the-author-block"
									)}
								</Button>
							)}
						/>
					</MediaUploadCheck>
					<div className="sptcrb__abouttheauthor__backend__wrapper-title">
						<TextControl
							label="Headline"
							placeholder={__("About The Author", "sptcrb")}
							value={attributes.headline}
							onChange={onUpdateHeadline}
						/>
					</div>
					<div className="sptcrb__abouttheauthor__backend__wrapper-text">
						<TextareaControl
							label="Author Bio"
							placeholder={__("About The Author", "sptcrb")}
							onChange={onUpdateBio}
							value={attributes.authorBio}
						/>
					</div>
					<div className="sptcrb__abouttheauthor__backend__wrapper-social-media-column">
						<TextControl
							label="Author Facebook Link"
							placeholder="https://facebook.com/"
							value={attributes.facebookLink}
							onChange={onUpdateFacebook}
						/>
					</div>
					<div className="sptcrb__abouttheauthor__backend__wrapper-social-media-column">
						<TextControl
							label="Author Twitter Link"
							placeholder="https://twitter.com/"
							value={attributes.twitterLink}
							onChange={onUpdateTwitter}
						/>
					</div>
					<div className="sptcrb__abouttheauthor__backend__wrapper-social-media-column">
						<TextControl
							label="Author LinkedIn Link"
							placeholder="https://linkedin.com/"
							value={attributes.linkedInLink}
							onChange={onUpdateLinkedIn}
						/>
					</div>
				</PanelBody>
				<PanelBody title={defaultTitle} initialOpen={true}>
					{defaultView()}
				</PanelBody>
			</InspectorControls>
			{savef(props)}
		</Fragment>
	);
};
